import {Button, Nav} from "react-bootstrap";
import "./OfferPage.css"
import {StepOne} from "./Components/StepOne/StepOne";
import {useState} from "react";
import {StepTwo} from "./Components/StepTwo/StepTwo";
import {StepThree} from "./Components/StepThree/StepThree";
import {useNavigate} from "react-router-dom";

export function OfferPage() {

    let [currentStep, setCurrentStep] = useState(1);
    const navigate = useNavigate()

    return (
        <>
            <div className="mt-5 w-auto">
                <Nav variant="pills" className="justify-content-center" defaultActiveKey="1" activeKey={currentStep}>
                    <Nav.Item className="me-4">
                        <Nav.Link className="px-xl-5" eventKey="1" onClick={() => setCurrentStep(1)}>
                            <span className="display-6">STEP 1</span>
                        </Nav.Link>
                    </Nav.Item>
                    <Nav.Item className="me-4">
                        <Nav.Link className="px-xl-5" eventKey="2" onClick={() => setCurrentStep(2)}>
                            <span className="display-6">STEP 2</span>
                        </Nav.Link>
                    </Nav.Item>
                    <Nav.Item>
                        <Nav.Link className="px-xl-5" eventKey="3" onClick={() => setCurrentStep(3)}>
                            <span className="display-6">STEP 3</span>
                        </Nav.Link>
                    </Nav.Item>
                </Nav>

                <div className="d-flex flex-column align-items-center w-100">
                    <div className="mt-5 d-flex flex-grow-1">
                        <Tab currentTab={currentStep}></Tab>
                    </div>
                    <div className=" d-flex justify-content-around mt-4 w-50">
                        <Button variant="primary" hidden={currentStep === 1} onClick={() => setCurrentStep(currentStep - 1)}>Previous</Button>
                        <Button variant="primary" hidden={currentStep !== 3} onClick={() => {navigate("/status")}}>Submit</Button>
                        <Button variant="primary" hidden={currentStep === 3} onClick={() => setCurrentStep(currentStep + 1)}>Next</Button>
                    </div>
                </div>
            </div>
        </>
    )
}

// @ts-ignore
function Tab({currentTab}): any {
    if (currentTab === 1) {
        return (<StepOne></StepOne>)
    } else if (currentTab === 2) {
        return (<StepTwo></StepTwo>)
    } else {
        return (<StepThree></StepThree>)
    }
}
